<template>
    <div>
        <el-container>
            <el-header>
                tlias 智能学习辅助系统
            </el-header>
        <el-container>

            <el-aside width="220px" style="border : 1px solid #eee">
                <el-menu :default-openeds="['1', '3']">
                    <el-submenu index="1">
                        <template slot="title"><i class="el-icon-message"></i>系统信息管理</template>
                        <el-menu-item index="1-1">
                            <router-link to="/dept">部门管理</router-link>
                        </el-menu-item>
                        <el-menu-item index="1-2">
                            <router-link to="/emp">员工管理</router-link>
                        </el-menu-item>
                    </el-submenu>
                </el-menu>
            </el-aside>

            <el-main>
                <el-form :inline="true" :model="searchForm" class="demo-form-inline">
                    <el-form-item label="姓名">
                        <el-input v-model="searchForm.name" placeholder="姓名"></el-input>
                    </el-form-item>
                        
                    <el-form-item label="性别">
                        <el-select v-model="searchForm.gender" placeholder="性别">
                            <el-option label="男" value="1"></el-option>
                            <el-option label="女" value="2"></el-option>
                        </el-select>
                    </el-form-item> 
                    <el-form-item label="入职日期">
                        <div class="block">
                                <el-date-picker
                                v-model="searchForm.entrydate"
                                type="daterange"
                                align="right"
                                unlink-panels
                                range-separator="至"
                                start-placeholder="开始日期"
                                end-placeholder="结束日期"
                                :picker-options="pickerOptions">
                                </el-date-picker>
                        </div>
                    </el-form-item>

                <el-form-item>
                    <el-button type="primary" @click="onSubmit">查询</el-button>
                </el-form-item>
               
                </el-form>

                <el-table :data="tableData" border>
                    <el-table-column prop="name" label="姓名" width="180">
                    </el-table-column>
                    <el-table-column label="照片" width="180">
                        <template slot-scope="scope">
                            <img :src="scope.row.image" width="100px" height="70px">
                        </template>
                    </el-table-column>
                    <el-table-column label="性别" width="140">
                        <template slot-scope="scope">
                            {{scope.row.gender == 1 ? '男':'女'}}
                        </template>
                    </el-table-column>
                    <el-table-column prop="job" label="职位" width="140">
                    </el-table-column>
                    <el-table-column prop="salary" label="薪资" width="140">
                    </el-table-column>
                    <el-table-column prop="entrydata" label="入职日期" width="180">
                    </el-table-column>
                    <el-table-column prop="updatetime" label="最后操作时间" width="230">
                    </el-table-column>
                    <el-table-column label="操作">
                        <el-button type="primary" size="mini">编辑</el-button>
                        <el-button type="danger" size="mini">删除</el-button>
                    </el-table-column>
                </el-table>
                <br>
                <el-pagination
                    background
                    layout="total,sizes,prev, pager, next,jumper"
                    @size-change = "handleSizeChange"
                    @current-change = "handleCurrentChange"
                    :total="1000">
                </el-pagination>

            </el-main>
        </el-container>
        </el-container>
    </div>
</template>

<script>

import axios from 'axios';

export default {
    data(){
        return {
            tableData:[],
            searchForm: {
                name:"",
                gender:"",
                entrydate:[]
            }
        }
    },
    methods:{
        onSubmit:function(){
            window.alert("查询数据");
        },
        handleSizeChange:function(val){
            window.alert("每页记录数变为" + val)
        },
        handleCurrentChange:function(val){
            window.alert("页码数变为" + val)
        }
    },
    mounted () {
        axios.get('http://127.0.0.1:4523/m1/6353946-6049592-default/user/').then((result) => {
            this.tableData = result.data.data;
        });
    }
}
</script>

<style>
    .el-header, .el-footer {
    background-color: #2dc2f0;
    color: #ffffff;
    text-align: center;
    line-height: 60px;
    font-size:40px;
  }
  
  .el-aside {
    background-color: #ffffff;
    color: #333;
    text-align: center;
    line-height: 200px;
  }
  
  body > .el-container {
    margin-bottom: 40px;
  }
  
  .el-container:nth-child(5) .el-aside,
  .el-container:nth-child(6) .el-aside {
    line-height: 260px;
  }
  
  .el-container:nth-child(7) .el-aside {
    line-height: 320px;
  }
</style>